.footer {
    padding: 20px 0;

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 992px) {
            flex-direction: column;
        }
    }

    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 992px) {
            margin-top: 20px;
        }

        @media (max-width: 576px) {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-gap: 20px;
        }
    }

    &__item + &__item {
        margin-left: 15px;

        @media (max-width: 576px) {
            margin-left: initial;
        }
    }

    &__copyright {
        @media (max-width: 767px) {
            text-align: center;
        }
    }

    a {
        color: var(--clr-primary-400);
    }
}