.header {
  padding-top: 15px;
  padding-bottom: 15px;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  max-width: 160px;
}

.header__logo img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.header__nav-list {
  display: flex;
  align-items: center;
}

.header__nav-item + .header__nav-item {
  margin-left: 30px;
}

.header__nav-link {
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;
  display: block;
}

.header__nav-link::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  width: 0;
  height: 2px;
  background-color: var(--clr-default-900);
  transform: translateX(-50%);
  transition: width 0.3s;
}

.header__burger {
  display: none;
}

.hero {
  position: relative;
  padding-top: 55px;
  padding-bottom: 615px;
  background-image: url("../img/hero/bg@2x.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero__btns {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero__btn {
  text-transform: uppercase;
}

.hero__btn + .hero__btn {
  margin-left: 15px;
}

.sources {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: var(--clr-default-900);
}

.sources__list {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-column-gap: 120px;
  grid-template-columns: repeat(4, 1fr);
}

.looking {
  padding-top: 50px;
  padding-bottom: 20px;
}

.looking .section__text {
  max-width: 780px;
}

.looking__list {
  display: flex;
  align-items: center;
  justify-items: center;
  margin-top: 70px;
}

.looking__item {
  max-width: 580px;
}

.looking__item + .looking__item {
  margin-left: 20px;
}

.electric {
  padding-top: 55px;
  padding-bottom: 505px;
  background-image: url("../img/skateboards/bg@2x.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.electric__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.electric__suptitle {
  margin-bottom: 10px;
}

.electric .section__text {
  max-width: 500px;
}

.electric__btn {
  margin-top: 20px;
}

.rev {
  padding-top: 45px;
  padding-bottom: 45px;
}

.rev__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.rev__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 15px;
}

.rev__item {
  max-width: 380px;
}

.rev__item img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.rev__item + .rev__item {
  margin-left: 20px;
}

.rev__info {
  max-width: 340px;
}

.rev__text {
  margin-bottom: 25px;
  line-height: 150%;
}

.rev__btn {
  min-width: 100px;
}

.shop__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.videos {
  padding-top: 55px;
  padding-bottom: 65px;
}

.videos__list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.videos__item iframe {
  max-width: 560px;
}

.videos__item + .videos__item {
  margin-left: 20px;
}

.accessories {
  padding-bottom: 100px;
}

.accessories__slider {
  width: 100%;
  height: 300px;
}

.accessories .section__title--small {
  margin-bottom: 40px;
}

.accessories .swiper-button-next:after,
.accessories .swiper-button-prev:after {
  font-size: 20px;
  color: #000000;
}

.accessories .swiper-button-next,
.accessories .swiper-button-prev {
