  height: 44px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.75);
}

.accessories__slide {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.accessories__slide--sale picture {
  position: relative;
}

.accessories__slide--sale picture::before {
  content: 'Sale';
  position: absolute;
  top: 0;
  right: 0;
  width: 46px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--clr-primary-400);
  color: var(--clr-default-900);
}

.accessories__img {
  max-width: 186px;
}

.accessories__img img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.accessories__title {
  margin-top: 15px;
  margin-bottom: 5px;
  font-size: 1.4rem;
  line-height: 130%;
  text-align: center;
  color: var(--clr-primary-400);
}

.accessories__text {
  text-align: center;
  line-height: 130%;
}

.accessories__text--sale {
  color: var(--clr-primary-400);
}

.accessories__text--sold {
  color: rgba(0, 0, 0, 0.3);
}

.specifications {
  padding: 35px 30px 75px;
  background-color: var(--clr-default-900);
  color: var(--clr-default-100);
  text-align: center;
}

.specifications__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  grid-column-gap: 75px;
  list-style: none;
}

.specifications__title {
  margin-top: 5px;
  margin-bottom: 25px;
  font-weight: 700;
  font-size: clamp(1.8rem, 1.32727rem + 1.47727vw, 3.1rem);
  line-height: 116%;
}

.specifications__text {
  max-width: 300px;
}

.specifications__item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.about {
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: var(--clr-default-300);
}

.about__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.about__img {
  margin-right: 15px;
  width: 100%;
  max-width: 160px;
}

.about__img img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.about__info {
  max-width: 600px;
}

.about__title {
  margin-bottom: 15px;
  font-weight: 700;
  font-size: clamp(1.6rem, 1.45455rem + 0.45455vw, 2rem);
  line-height: 150%;
}

.about__text {
  margin-bottom: 20px;
}

.about__address p + p {
  margin-top: 5px;
}

.footer {
  padding: 20px 0;
}

.footer__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__item + .footer__item {
  margin-left: 15px;
}

.footer a {
  color: var(--clr-primary-400);
}

@media (max-width: 1200px) {
  .sources__list {
    grid-column-gap: 60px;
  }

  .videos__item iframe {
    max-width: 460px;
  }

  .specifications__text {
    max-width: initial;
  }
}

@media (max-width: 992px) {
  .section__pb {
    padding-bottom: 400px;
  }

  .header__nav {
    position: fixed;
    right: -100%;
    top: 0;
    z-index: 10;
    display: flex;
    overflow: auto;
    width: 100%;
    height: 100%;
    max-width: 320px;
    background-color: var(--clr-default-900);
    transition: right 0.3s;
  }

  .header__nav--active {
    right: 0;
    transition: right 0.3s;
  }

  .header__nav-list {
    flex-direction: column;
    margin: auto;
    padding-block: 40px;
  }

  .header__nav-item + .header__nav-item {
    margin-top: 30px;
    margin-left: initial;
  }

  .header__nav-link {
    font-size: 2.4rem;
    color: var(--clr-default-100);
  }

  .header__burger {
    position: relative;
    z-index: 15;
    display: block;
    width: 25px;
    height: 20px;
    background-color: transparent;
  }

  .header__burger::after,
  .header__burger::before,
  .header__burger span {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: var(--clr-default-900);
  }

  .header__burger::before {
    top: 0;
    transition: transform 0.3s, top 0.3s;
  }

  .header__burger::after {
    bottom: 0;
    transition: transform 0.3s, bottom 0.3s;
  }

  .header__burger span {
    top: 8px;
    transition: transform 0.3s;
  }

  .header__burger--active::after,
  .header__burger--active::before,
  .header__burger--active span {
    background-color: var(--clr-default-100);
  }

  .header__burger--active span {
    transform: scale(0);
    transition: transform 0.3s;
  }

  .header__burger--active::before {
    top: 8px;
    transform: rotate(45deg);
    transition: transform 0.3s, top 0.3s;
  }

  .header__burger--active::after {
    bottom: 8px;
    transform: rotate(-45deg);
    transition: transform 0.3s, bottom 0.3s;
  }

  .sources__list {
    grid-column-gap: 20px;
  }

  .looking__list {
    flex-direction: column;
  }

  .looking__item + .looking__item {
    margin-left: initial;
    margin-top: 20px;
  }

  .rev__wrapper {
    flex-direction: column-reverse;
  }

  .rev__info {
    margin-bottom: 20px;
    max-width: initial;
  }

  .shop__list {
    grid-template-columns: repeat(2, 1fr);
  }

  .shop__item:last-child {
    grid-column: 1/3;
  }

  .videos__list {
    flex-direction: column;
  }

  .videos__item iframe {
    max-width: initial;
  }

  .videos__item + .videos__item {
    margin-left: initial;
    margin-top: 20px;
  }

  .specifications__list {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }

  .footer__wrapper {
    flex-direction: column;
  }

  .footer__list {
    margin-top: 20px;
  }
}

@media (max-width: 767px) {
  .section__pb {
    padding-bottom: 300px;
  }

  .sources__list {
    grid-gap: 20px;
    grid-template-columns: repeat(2, 1fr);
  }

  .looking {
    padding-top: 30px;
  }

  .rev {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .videos {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .videos__item iframe {
    max-width: 460px;
  }

  .accessories {
    padding-bottom: 30px;
  }

  .specifications {
    padding: 30px 15px;
  }

  .about__wrapper {
    flex-direction: column;
  }

  .about__img {
    margin-right: initial;
  }

  .about__info {
    max-width: initial;
  }

  .footer__copyright {
    text-align: center;
  }
}

@media (max-width: 576px) {
  .section__pb {
    padding-bottom: 200px;
  }

  .sources__list {
    display: block;
  }

  .sources__item {
    text-align: center;
  }

  .sources__item + .sources__item {
    margin-top: 30px;
  }

  .rev__list {
    flex-direction: column;
    margin-right: initial;
  }

  .rev__item + .rev__item {
    margin-left: initial;
    margin-top: 20px;
  }

  .shop__list {
    display: block;
  }

  .shop__item:last-child {
    grid-column: initial;
  }

  .shop__item {
    width: 100%;
  }

  .videos__item iframe {
    max-width: 400px;
  }

  .specifications__list {
    display: block;
  }

  .specifications__item + .specifications__item {
    margin-top: 20px;
  }

  .footer__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
  }

  .footer__item + .footer__item {
    margin-left: initial;
  }
}

@media (max-width: 460px) {
  .hero__btns {
    flex-direction: column;
  }

  .hero__btn + .hero__btn {
    margin-top: 20px;
    margin-left: initial;
  }

  .videos__item iframe {
    max-width: 340px;
  }
}

@media (max-width: 360px) {
  .videos__item iframe {
    max-width: 300px;
    width: 100%;
    height: auto;
  }
}

@media (hover) {
  .section__btn:hover {
    outline: 1px solid var(--clr-primary-400);
    background-color: var(--clr-default-100);
    color: var(--clr-primary-400);
    transition: outline 0.3s, background-color 0.3s, color 0.3s;
  }

  .section__scale img:hover {
    cursor: pointer;
    transform: scale(1.1);
  }

  .header__nav-link:hover::before {
    width: 100%;
    transition: width 0.3s;
  }
}

