.section {

    &__pb {
        @media (max-width: 992px) {
            padding-bottom: 400px;
        }

        @media (max-width: 767px) {
            padding-bottom: 300px;
        }

        @media (max-width: 576px) {
            padding-bottom: 200px;
        }
    }

    &__title {
        font-weight: 700;

        &--large {
            margin-bottom: 15px;
            font-size: fluid-font(4.4rem, 2.8rem);
        }

        &--small {
            margin-bottom: 20px;
            font-size: fluid-font(3.2rem, 2rem);
        }

        &--center {
            text-align: center;
        }
    }

    &__text {
        margin: 0 auto;
        text-align: center;
        letter-spacing: 1px;
        line-height: 125%;
    }

    &__btn {
        height: 35px;
        overflow: hidden;
        padding: 0 15px;
        background-color: var(--clr-primary-400);
        border-radius: 35px;
        transition: outline $ms, background-color $ms, color $ms;
        color: var(--clr-default-100);

        @media (hover) {
            &:hover {
                outline: 1px solid var(--clr-primary-400);
                background-color: var(--clr-default-100);
                color: var(--clr-primary-400);

                transition: outline $ms, background-color $ms, color $ms;
            }
        }

        &-mw200 {
            min-width: 200px;
        }
    }

    &__line {
        position: relative;

        &::after {
            content: '';
            position: absolute;
            bottom: -25px;
            left: 50%;
            transform: translateX(-50%);
            width: 70px;
            height: 2px;
            background-color: var(--clr-default-200);
        }
    }

    &__scale {
        display: inline-block;
        overflow: hidden;

        img {
            transition: 1s;
            display: block;

            @media (hover) {
                &:hover {
                    cursor: pointer;
                    transform: scale(1.1);
                }
            }
        }
    }

    &__item {
        position: relative;

        &-info {
            position: absolute;
            left: 50%;
            top: 50%;
            text-align: center;
            color: var(--clr-default-100);
            transform: translate(-50%);
        }

        &-title {
            margin-bottom: 15px;
            font-size: fluid-font(2.5rem, 1.8rem);
            line-height: 120%;
        }

        &-text {
            font-size: fluid-font(1.8rem, 1.6rem);
        }

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }
}