.videos {
    padding-top: 55px;
    padding-bottom: 65px;

    @media (max-width: 767px) {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    &__list {
        display: flex;
        align-items: center;
        justify-content: center;

        @media (max-width: 992px) {
            flex-direction: column;
        }
    }

    &__item {
        iframe {
            max-width: 560px;

            @media (max-width: 1200px) {
                max-width: 460px;
            }

            @media (max-width: 992px) {
                max-width: initial;
            }

            @media (max-width: 767px) {
                max-width: 460px;
            }

            @media (max-width: 576px) {
                max-width: 400px;
            }

            @media (max-width: 460px) {
                max-width: 340px;
            }

            @media (max-width: 360px) {
                max-width: 300px;
                width: 100%;
                height: auto;
            }
        }
    }

    &__item + &__item {
        margin-left: 20px;
        @media (max-width: 992px) {
            margin-left: initial;
            margin-top: 20px;
        }
    }
}