$burgerMQ: 992px;
.header {
    padding-top: 15px;
    padding-bottom: 15px;

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
        max-width: 160px;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    &__nav {
        @media (max-width: $burgerMQ) {
            position: fixed;
            right: -100%;
            top: 0;
            z-index: 10;
            display: flex;
            overflow: auto;
            width: 100%;
            height: 100%;
            max-width: 320px;
            background-color: var(--clr-default-900);
            transition: right $ms;

            &--active {
                right: 0;
                transition: right $ms;
            }
        }
    }

    &__nav-list {
        display: flex;
        align-items: center;

        @media (max-width: $burgerMQ) {
            flex-direction: column;
            margin: auto;
            padding-block: 40px;
        }
    }

    &__nav-item + &__nav-item {
        margin-left: 30px;

        @media (max-width: $burgerMQ) {
            margin-top: 30px;
            margin-left: initial;
        }
    }

    &__nav-link {
        letter-spacing: 1px;
        text-transform: uppercase;


        position: relative;
        display: block;

        &::before {
            content: "";
            position: absolute;
            left: 50%;
            bottom: -5px;
            width: 0;
            height: 2px;
            background-color: var(--clr-default-900);
            transform: translateX(-50%);
            transition: width $ms;
        }

        @media (hover) {
            &:hover {
                &::before {
                    width: 100%;
                    transition: width $ms;
                }
            }
        }

        @media (max-width: $burgerMQ) {
            font-size: 2.4rem;
            color: var(--clr-default-100);
        }
    }


    &__burger {
        $width: 25px;
        $height: 20px;
        $bgColor: var(--clr-default-900);
        $burgerLineHeight: 4px;

        display: none;

        @media (max-width: $burgerMQ) {
            position: relative;
            z-index: 15;
            display: block;
            width: $width;
            height: $height;
            background-color: transparent;

            &::after,
            &::before,
            span {
                content: "";
                position: absolute;
                left: 0;
                width: 100%;
                height: $burgerLineHeight;
                background-color: $bgColor;
            }

            &::before {
                top: 0;
                transition: transform $ms, top $ms;
            }
            &::after {
                bottom: 0;
                transition: transform $ms, bottom $ms;
            }
            span {
                top: 8px;
                transition: transform $ms;
            }

            &--active {
                &::after,
                &::before,
                span {
                    background-color: var(--clr-default-100);
                }

                span {
                    transform: scale(0);
                    transition: transform $ms;
                }

                &::before {
                    top: 8px;
                    transform: rotate(45deg);
                    transition: transform $ms, top $ms;
                }

                &::after {
                    bottom: 8px;
                    transform: rotate(-45deg);
                    transition: transform $ms, bottom $ms;
                }
            }
        }
    }

}
