.specifications {
    padding: 35px 30px 75px;
    background-color: var(--clr-default-900);
    color: var(--clr-default-100);
    text-align: center;

    @media (max-width: 767px) {
        padding: 30px 15px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        align-items: center;
        grid-column-gap: 75px;

        list-style: none;

        @media (max-width: 992px) {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 30px;
        }

        @media (max-width: 576px) {
           display: block;
        }
    }

    &__title {
        margin-top: 5px;
        margin-bottom: 25px;
        font-weight: 700;
        font-size: fluid-font(3.1rem, 1.8rem);
        line-height: 116%;
    }

    &__text {
        max-width: 300px;

        @media (max-width: 1200px) {
            max-width: initial;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &__item + &__item {
        @media (max-width: 576px) {
            margin-top: 20px;
        }
    }
}