.electric {
    padding-top: 55px;
    padding-bottom: 505px;
    background-image: url('../img/skateboards/bg@2x.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;


    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &__suptitle {
        margin-bottom: 10px;
    }

    .section__text {
        max-width: 500px;
    }

    &__btn {
        margin-top: 20px;
    }
}