.accessories {
    padding-bottom: 100px;

    @media (max-width: 767px) {
        padding-bottom: 30px;
    }

    &__slider {
        width: 100%;
        height: 300px;
    }

    .section__title--small {
        margin-bottom: 40px;
    }

    .swiper-button-next:after, .swiper-button-prev:after {
        font-size: 20px;
        color: #000000;
    }

    .swiper-button-next, .swiper-button-prev {
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.75);
    }

    &__slide {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        &--sale {

            picture {
                position: relative;

                &::before {
                    content: 'Sale';
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 46px;
                    height: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: var(--clr-primary-400);
                    color: var(--clr-default-900);
                }
            }
        }
    }

    &__img {
        max-width: 186px;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    &__title {
        margin-top: 15px;
        margin-bottom: 5px;
        font-size: 1.4rem;
        line-height: 130%;
        text-align: center;
        color: var(--clr-primary-400);
    }

    &__text {
        text-align: center;
        line-height: 130%;

        &--sale {
            color: var(--clr-primary-400);
        }

        &--sold {
            color: rgba(0, 0, 0, 0.3);
        }
    }
}