.shop {

    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);

        @media (max-width: 992px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media (max-width: 576px) {
            display: block;
        }
    }

    &__item {

        &:last-child {
            @media (max-width: 992px) {
                grid-column: 1/3;
            }

            @media (max-width: 576px) {
                grid-column: initial;
            }
        }

        @media (max-width: 576px) {
            width: 100%;
        }
    }


}