.rev {
    padding-top: 45px;
    padding-bottom: 45px;

    @media (max-width: 767px) {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 992px) {
            flex-direction: column-reverse;
        }
    }

    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-right: 15px;

        @media (max-width: 576px) {
            flex-direction: column;
            margin-right: initial;
        }
    }

    &__item {
        max-width: 380px;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    &__item + &__item {
        margin-left: 20px;

        @media (max-width: 576px) {
            margin-left: initial;
            margin-top: 20px;
        }
    }

    &__info {
        max-width: 340px;

        @media (max-width: 992px) {
            margin-bottom: 20px;
            max-width: initial;
        }
    }

    &__text {
        margin-bottom: 25px;
        line-height: 150%;
    }

    &__btn {
        min-width: 100px;
    }
}