.about {
    padding-top: 50px;
    padding-bottom: 50px;
    background-color: var(--clr-default-300);

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 767px) {
            flex-direction: column;
        }
    }

    &__img {
        margin-right: 15px;
        width: 100%;
        max-width: 160px;

        @media (max-width: 767px) {
            margin-right: initial;
        }

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    &__info {
        max-width: 600px;

        @media (max-width: 767px) {
            max-width: initial;
        }
    }

    &__title {
        margin-bottom: 15px;
        font-weight: 700;
        font-size: fluid-font(2rem, 1.6rem);
        line-height: 150%;
    }

    &__text {
        margin-bottom: 20px;
    }

    &__address {
        p + p {
            margin-top: 5px;
        }
    }
}