.looking {
    padding-top: 50px;
    padding-bottom: 20px;

    @media (max-width: 767px) {
        padding-top: 30px;
    }

    .section__text {
        max-width: 780px;
    }

    &__list {
        display: flex;
        align-items: center;
        justify-items: center;
        margin-top: 70px;

        @media (max-width: 992px) {
            flex-direction: column;
        }
    }

    &__item {
        max-width: 580px;
    }

    &__item + &__item {
        margin-left: 20px;

        @media (max-width: 992px) {
            margin-left: initial;
            margin-top: 20px;
        }
    }
}